import React, { PureComponent } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../utils";
import { isTagShop, isTagEmbed, suiteTrack, isTagbox } from "../../../../actions/api";
import Network from "../../Elements/Network/Network";
import ShopButton from "../../Elements/ShopButton/ShopButton";
import ViewCount from "../../Elements/ViewCount";


export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    const { itemData, onClickPopUpSlider, itemIndex } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.mediaHeight)
    if (itemData.isPopUp) {
      onClickPopUpSlider(itemIndex, itemData)
    }
  }

  render() {
    const { itemData, itemIndex, wallID, ownerId, wall, isVisible, clickToShowPopUp, ThemeRule, personalization } = this.props;
    const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
    const cardStyle = { backgroundColor: ThemeRule.cardColor };
    const isShopBtn = isTagShop && (isProduct) && !ThemeRule.hideContent ? true : false;
    const isShopIcon = isTagShop && (isProduct && itemData.hotspot === 0) ? true : false
    const isHotspot = isTagShop && itemData.hotspot && isProduct ? true : false
    const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
    const isVideo = (itemData.type === 3 || itemData.type === 5) ? true : false


    return <div item-id={itemData.id} id={`tb-hc-post-${itemData.id}`} className="tb_rt_post_wrapper" ref={this.mediaHeight} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} style={{ padding: wall.Personalization.padding / 2 }} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} area-label={itemData.content} role="article" tabIndex="0">
      <div className={`tb_rt_post_in tb_icon_animate ${!isTagShop ? 'tb_rt_post_in_animate' : ''}`} style={cardStyle} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
        <div className="tb_rt_post_media_wrapp" role="button" tabindex="0" aria-label="Pop Up Button">
          {isVideo && ThemeRule.showReelsView == 1 && itemData?.views > 0 ? <ViewCount count={itemData?.views} /> : null}

          <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} preload={itemIndex == 0 ? true : false} isVisible={isVisible} />
          {!isTagShop ?
            <div className="tb_rt_play_ico">
              <div className="tb__icon tb-play"><div></div></div>
            </div>
            : null}
          <div className="tb_rt_post_details">
            <div className="tb_rt_social_">
              <Network networkClass={'tb_rt_social_ico_'} network={itemData.network} ThemeRule={ThemeRule} isDefault={ThemeRule.iconType === 1 ? true : false} Personalization={personalization} />
            </div>
            {isShopBtn ? <ShopButton ThemeRule={ThemeRule} btnClass={'tb_rt_btn_wrap'} /> : null}
          </div>
          <div className={`tb_rt_post_overlay tb_rt_post_overlay-${themeMode}`}><div></div></div>
        </div>
      </div>
    </div>
  }
}
