import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import { isTagShop } from "../../../../actions/api";
const Media = ({ itemData, wallID, wall, ownerId, isVisible }) => {

  const isVideo = (itemData.type === 3 || itemData.type === 5) && isVisible && (wall.ThemeRule.autoPlay == 1) ? true : false
  return <div className="tb_rt_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">
    <MediaIcon itemData={itemData} IconClass="tb_rt_media_icon" show={true} hideVideo={true} />
    {isVideo ?
      <Video VideoClass="tb_rt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={178} isCover={true} muted={true} autoPlay={true} />
      :
      <Image ImageClass="tb_rt_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={178} />
    }
  </div>
}
export default Media