import React, { Component, memo } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/free-mode';
import 'swiper/css/mousewheel';
import './scss/ReelTheme.scss'

import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'
import { isTagShop, isTagbox } from '../../../actions/api'

class ReelTheme extends Component {
  constructor(props) {
    super(props)
    this.inputRefs = {};
  }
  requestData = (swiper) => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps, loaderData } = this.props;
    if (swiper.progress > 0.8 && hasMoreData && !loaderData.isShowMoreLoading) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
    const { postData } = this.props;

    if (postData && postData.length == 1) {

      let arrow_left_id = document.querySelector(".tb_rt_arrow_left__")
      let arrow_right_id = document.querySelector(".tb_rt_arrow_right__")
      if (arrow_left_id && arrow_right_id) {
        document.querySelector(".tb_rt_arrow_left__").style = 'display:none'
        document.querySelector(".tb_rt_arrow_right__").style = 'display:none'
      }

    }
  }

  reScrolled = () => {

    setTimeout(() => {
      let splide__list = document.querySelector('.splide__list');
      let getWidth = document.querySelector(".splide__slide").offsetWidth;
      const isVisibleAll = document.querySelectorAll('.splide__slide.is-visible').length;
      const isVisible = document.querySelector('.splide__slide.is-active.is-visible');
      const str = isVisible.getAttribute('aria-label').substring(0, 2)
      const getIndex = Number(str) + (isVisibleAll - 1) + isVisibleAll
      if (getIndex > 0) {
        splide__list.style.transition = '0s ease';
        splide__list.style.transform = `translateX(-${(getIndex * getWidth)}px)`;

      } else {
        splide__list.style.transform = `translateX(0px)`;
      }
    }, 200);
  }


  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, parentID } = this.props;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      data.map((item, itemIndex) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)
          let clickD = document.querySelectorAll(`.tb_rt_post_container #${item.id}`)
          for (let index = 0; index < clickD.length; index++) {
            let clickDD = clickD[index];
            let p_id = clickDD.getAttribute('p_id')
            if (String(parentID) === String(p_id)) {
              clickDD.addEventListener('click', event => {
                event.preventDefault();
                const itemData = completeDataObject[getID]
                onClickPopUpSlider(findIndex, itemData)
              });
            }
          }
        }
      })
    }
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, parentID, keyRender, onClickPopUpSlider } = this.props;
    const inputName = parentID;
    //const { windowWidth } = this.state;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const trimContent = wall.Personalization.trimcontent === 1 ? true : false;
    const isSlidePerView = isTagbox && [151513].includes(wall.Wall.owner) ? 0 : 0.6
    return <>{parentID != "" ?
      <div className="tb_rt_post_container" ref={this.sliderHeight}>
        <Swiper
          key={keyRender}
          ref={(parentID) => this.inputRefs[inputName] = parentID}
          style={{ width: '100%' }}
          onScroll={(swiper) => this.requestData(swiper)}
          onSlideChange={(swiper) => this.requestData(swiper)}
          onAutoplay={(swiper) => this.requestData(swiper)}
          autoHeight={true}
          speed={700}
          edgeSwipeDetection={true}
          slidesPerView={'auto'}
          touchRatio={2}
          cssMode={isMobile}
          passiveListeners={true}
          height={'250px'}
          freeMode={{ enabled: true, sticky: false, momentumBounceRatio: 2, momentumVelocityRatio: 3 }}
          autoplay={isTagShop || isTagbox ? { delay: slidePost === 1 ? slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true, } : false}
          loop={!hasMoreData}
          breakpoints={{
            0: {
              slidesPerView: mobileColumn == 0 ? 1 + isSlidePerView : parseInt(mobileColumn) + isSlidePerView
            },
            480: {
              slidesPerView: mobileColumn == 0 ? 1 + isSlidePerView : parseInt(mobileColumn) + isSlidePerView
            },
            575: {
              slidesPerView: mobileColumn == 0 ? 2 + isSlidePerView : parseInt(mobileColumn) + isSlidePerView
            },
            767: {
              slidesPerView: numberOfCoumn == 0 ? 3 + isSlidePerView : parseInt(numberOfCoumn) + isSlidePerView
            },
            1024: {
              slidesPerView: numberOfCoumn == 0 ? 5 + isSlidePerView : parseInt(numberOfCoumn) + isSlidePerView
            },
            1400: {
              slidesPerView: numberOfCoumn == 0 ? 6 + isSlidePerView : parseInt(numberOfCoumn) + isSlidePerView
            },
            1600: {
              slidesPerView: numberOfCoumn == 0 ? 7 + isSlidePerView : parseInt(numberOfCoumn) + isSlidePerView
            }
          }}
          mousewheel={{ forceToAxis: true, sensitivity: 2 }}
          watchSlidesProgress={true}
          modules={[Autoplay, Mousewheel, FreeMode]} className={`tb_rt_post_slider`}>
          <>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return (isTagShop || (cardData.type != 1)) ? <SwiperSlide style={{ margin: 0, padding: 0, }} key={index} p_id={parentID} className={`tb_${cardData.id}`} >
                  {({ isVisible }) => <Card isVisible={isVisible} ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} wall={wall} onClickPopUpSlider={onClickPopUpSlider} />}
                </SwiperSlide> : null
              })}
          </>
        </Swiper>
      </div> : null}</>
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(ReelTheme));